#intro{
    height: calc(100vh-4rem);
    width:100vw;
    max-width:75rem;
    margin:0 auto;
    overflow:hidden;
    position: relative

}
.bg{
    position: absolute;
    top: -4rem;
    right:-2rem;
    z-index: -1;
    object-fit: cover;
    height: 100vh;
   
}
.intro-content{
    height: 100vh;
    width: 100vm;
    margin:0 ;
    font-size: 3rem;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color:white;

}

.hello{
    font-size: 1.75rem;
    font-weight: 100;

} 
.intro-name{
    color:yellow;
}
.intro-para{
    font-weight: 300;
    font-size:small;
     letter-spacing: 1px ;
}

.btn-hireme{
    background-color: white;
    margin: 1rem 0;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius: 2rem;
}
.btnImg{  
    object-fit: cover;
    margin: 0 0.25rem;
    height:1rem; 
}

@media screen and (max-width:840px){
    .bg{
        right:-10vw;
    }
    .intro-content{
        font-size: 10vw;
    }
    .hello{
        font-size: 4.5vw;
    }
    
}
@media screen and (max-width:480px){
    .bg{
        right:-30vw;
    }
    
    
}

